(function() {
	'use strict';

	angular.module('webapp').directive('acmeNavbar', acmeNavbar);

	/** @ngInject */
	function acmeNavbar() {
		var directive = {
			restrict : 'E',
			templateUrl : 'app/components/navbar/navbar.html',
			scope : {
				creationDate : '='
			},
			controller : NavbarController,
			controllerAs : 'vm',
			bindToController : true
		};

		return directive;

		/** @ngInject */
		function NavbarController(moment, $rootScope,$timeout) {
			var vm = this;
			vm.irParaRep = function() {
				$timeout.cancel();
				$rootScope.$state.go('repListar');
			}
			vm.irParaHome = function() {
				$rootScope.$state.go('home');
			}
			// "vm.creationDate" is available by directive option
			// "bindToController: true"
			vm.relativeDate = moment(vm.creationDate).fromNow();
		}
	}

})();

(function() {
  'use strict';

  angular
    .module('webapp')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function() {
/*          vm.contributors = data;*/

          return vm.contributors;
       });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('webapp')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
	'use strict';

	angular.module('webapp').controller('MainController', MainController);

	/** @ngInject */
	function MainController(RepService, $timeout, $rootScope) {
		var vm = this;
		vm.ismonitorando = true;
		vm.listaRep = [];

		function monitorar() {
			RepService.repStatus.query(function(response) {
				vm.listaRep = response;
				$rootScope.monitorar = $timeout(monitorar, 1000);
			}, function() {
				$rootScope.monitorar = $timeout(monitorar, 1000);
			});
		}

		vm.initStopMon = function(){
			if(vm.ismonitorando){
				$rootScope.cancelarMonitoramento();
				vm.ismonitorando = false;
			}else{
				monitorar();
				vm.ismonitorando = true;
			}
		}

		monitorar();

	}

})();

(function() {
	'use strict';

	angular.module('webapp').factory('tokenInterceptor', tokenInterceptor);

	function tokenInterceptor($q, $rootScope,$log) {
		return {
			'request' : function($config) {
				$log.debug('tokenInterceptor');
				if (angular.isDefined($rootScope.authToken)) {
					var authToken = $rootScope.authToken;
					// if (TopPontoWebApp.useAuthTokenHeader) {
					$config.headers['X-Auth-Token'] = authToken;
				}
				return $config || $q.when($config);
			}
		};
	}

})();
(function() {
	'use strict';

	angular.module('webapp').config(interceptorConfig);

	function interceptorConfig($httpProvider) {

		$httpProvider.interceptors.push('tokenInterceptor');
		$httpProvider.defaults.useXDomain = true;
		$httpProvider.defaults.headers.common['Access-Control-Allow-Origin'] = '*';
		$httpProvider.defaults.headers.common['Access-Control-Allow-Methods'] = 'POST, GET, OPTIONS, PUT, DELETE, HEAD, PUT';
		$httpProvider.defaults.headers.common['Access-Control-Allow-Headers'] = 'X-PINGOTHER, Origin, X-Requested-With, Content-Type, Accept,*';
		$httpProvider.defaults.headers.common['Access-Control-Max-Age'] = '1728000';

	}

})();
(function() {
	'use strict';

	angular.module('webapp').run(runBlock);

	/** @ngInject */
	function runBlock($log, $rootScope, $state, $timeout,toastr) {
		$rootScope.$state = $state;
		$rootScope.toastr = toastr;
		$log.debug('app running');

		$rootScope.cancelarMonitoramento = function() {
			$timeout.cancel($rootScope.monitorar);
		}
	}

})();

